import axios from 'axios';
import apiPrefix from './apiPrefix';
import NProgress from 'nprogress';
import url from './utils/url';

const pending = {};
const CancelToken = axios.CancelToken;

const getRequestIdentify = (config, isRequest = false) => {
  let url = config.url;
  if (isRequest) { // 
    url = config.baseURL + config.url; // 
  }
  return config.method === 'get'
    ? encodeURIComponent(url + JSON.stringify(config.params))
    : encodeURIComponent(url + JSON.stringify(config.data));
};

const removePending = (key, isRequest = false) => {
  if (pending[key] && isRequest) { // 
    // console.log('[]');
    pending[key]('');
  }
  delete pending[key]; // 
};

/*
* :
* (1)requestData
* (2)pending[key]=undefined
* (3)pending[key]('');
* (4)delete pending[key] >>> delete undefined;
* (5)pending[key] = c
*
* : 
* (1)requestData
* (2)pending[key] = c
* (3)pending[key]('')
* (4)delete pending[key] = c;
* (5)pending[key] = c >>> 
* */

// 
axios.defaults.timeout = 10000;
axios.defaults.baseURL = apiPrefix.baseURL();
axios.defaults.withCredentials = true;

// 
axios.interceptors.request.use((config) => {
  NProgress.start();
  // console.log('[]', config);

  // 1.
  // (1)
  let requestData = getRequestIdentify(config, true);

  // (2)
  removePending(requestData, true);

  // (3) ()
  config.cancelToken = new CancelToken((c) => { // c
    pending[requestData] = c;
  });

  // 2., 
  if (config.url === url.getVerifyCode()) {
    config.responseType = 'blob';
    config.emulateJSON = true;
  }
  // console.log('[ pending]', pending);
  return config;
}, (error) => {
  return Promise.reject(error);
});

// 
axios.interceptors.response.use((config) => {
  NProgress.done();
  // console.log('[]', config);
  // console.log('[ pending]', pending);
  //  pending 
  let requestData = getRequestIdentify(config.config);
  removePending(requestData); // 

  // 200
  return config;
}, (error) => {
  NProgress.done();
  // console.log('[  error]', error);
  // console.log('[  error.message]', error.response);
  // 304, 400, 401, 404, 500
  return Promise.reject(error); // 
});

