import React from 'react';

// 
import ModalForm from '../modalform/modalform';

// utils
import {createFormList} from '../../utils/formFieldList';

// style.js
import {
  NoGroupLayout,
  GenericLayout,
  LocalHeaderGrid,
  LocalPageContent,
  PageContentGrid,
  ServicesArea,
  NoServicesText
} from './style';

// antd
import {Icon, Result, Button} from 'antd';

class NoGroupPage extends React.Component {
  render() {
    return (
      <NoGroupLayout>
        <GenericLayout>
          {/**/}
          <LocalHeaderGrid>
            <span></span>
          </LocalHeaderGrid>

          {/**/}
          <LocalPageContent>
            <PageContentGrid>
              <ServicesArea>
                <NoServicesText>
                  <Result
                    icon={<Icon type="usergroup-add"/>}
                    title=', '
                    extra={
                      <Button type='primary' size='large' onClick={this.showModal}>
                        
                      </Button>  
                    }
                  />
                  <ModalForm
                    title=''
                    okText={``}
                    formList={createFormList}
                    ref={(modal) => this.modalInstance = modal}
                    inputFormValue={(formValue) => {
                      // 
                      this.props.createGroup(formValue);
                    }}
                  />  
                </NoServicesText>
              </ServicesArea>
            </PageContentGrid>
          </LocalPageContent>
        </GenericLayout>
      </NoGroupLayout>
    )
  }

  showModal = () => {
    //   
    this.modalInstance.showModal();
  };
}

export default NoGroupPage;